.MatchDetailCard {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 10px;
  margin-bottom: 20px;

  .match-detail,
  .match-result,
  .match-venue {
    margin-top: 10px;
  }

  .additional-detail {
    text-align: right;

    & > p {
      margin-bottom: 20px;
    }
  }
}
